import React, { useEffect, useRef, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { motion } from "framer-motion";
import { toast } from "react-toastify";
import styles from "./styles.module.scss";
import { User } from "../../UserService";
import {
  getMyNfts,
  getMyWorkingNfts,
} from "../../GlobalState/NftsSlice/nftsSlice";
import Button from "../Button/Button";
import Loader from "../Loader/Loader";

import mineAurum from "../../assets/images/nfts/Mine-Aurum.webp";
import mineCelium from "../../assets/images/nfts/Mine-Celium.webp";
import buildingSlot from "../../assets/images/nfts/Building-Slot.webp";
import levelUpToken from "../../assets/images/nfts/Level-Up-Token.webp";
import teleportToChaos from "../../assets/images/nfts/Teleport.webp";

const MyNftCard = React.lazy(() =>
  import("../../components/Nft/MyNftCard/MyNftCard")
);

const images = {
  "Chaos X-18 Mine Aurum": mineAurum,
  "ChaosX-18 Mine Celium": mineCelium,
  "ChaosX-18 Building Slot": buildingSlot,
  "ChaosX-18 Level Up token": levelUpToken,
  "Teleport to ChaosX-18": teleportToChaos,
};

const Mining = ({ mine }) => {
  const scrollRef = useRef(null);
  const dispatch = useDispatch();
  const [buttonLoader, setButtonLoader] = useState(null);
  const { waxConnected, anchorConnected } = useSelector((state) => state.user);
  const { myWorkingNfts, myNfts } = useSelector((state) => state.nfts);
  const miningTransition = {
    type: "spring",
    ease: "easeInOut",
    damping: 35,
    delay: 0.5,
  };
  const Variants = {
    initial: { opacity: 0 },
    whileInView: { opacity: 1 },
  };

  // mine
  const mineWithAnchor = () => {
    if (buttonLoader) return;

    setButtonLoader("Mine");
    User.anchorSession
      ?.transact(
        {
          actions: [
            {
              account: "xcryptochaos",
              name: "minetoken",
              authorization: [
                {
                  actor: User.anchorSession?.auth?.actor.toString(),
                  permission: "active",
                },
              ],
              data: {
                player: User.anchorSession?.auth?.actor.toString(),
                mineId: mine?.id,
              },
            },
          ],
        },
        {
          blocksBehind: 3,
          expireSeconds: 30,
        }
      )
      .then((_) => {
        toast.success("Resources collecting has successfully started");
        setButtonLoader(null);
      })
      .catch((error) => {
        toast.error("You can't claim now, upgrade in progress");
        setButtonLoader(null);
      });
  };

  const mineWithWax = () => {
    if (buttonLoader) return;

    setButtonLoader("Mine");
    User.wax.api
      .transact(
        {
          actions: [
            {
              account: "xcryptochaos",
              name: "minetoken",
              authorization: [
                {
                  actor: User.wax?.userAccount,
                  permission: "active",
                },
              ],
              data: {
                player: User.wax?.userAccount,
                mineId: mine?.id,
              },
            },
          ],
        },
        {
          blocksBehind: 3,
          expireSeconds: 30,
        }
      )
      .then((_) => {
        toast.success("Resources collecting has successfully started");
        setButtonLoader(null);
      })
      .catch((error) => {
        toast.error("You can't claim now, upgrade in progress");
        setButtonLoader(null);
      });
  };

  const mineNft = () => {
    if (anchorConnected) {
      mineWithAnchor();
    } else if (waxConnected) {
      mineWithWax();
    }
  };

  // upgrade
  const upgradeWithAnchor = (nft, levelUp) => {
    if (buttonLoader) return;

    setButtonLoader(nft.asset_id);
    User.anchorSession
      ?.transact(
        {
          actions: [
            {
              account: "atomicassets",
              name: "transfer",
              authorization: [
                {
                  actor: User.anchorSession?.auth?.actor.toString(),
                  permission: "active",
                },
              ],
              data: {
                from: User.anchorSession?.auth?.actor.toString(),
                to: "xcryptochaos",
                asset_ids: [levelUp.asset_id],
                memo: "burn@level@" + nft.asset_id,
              },
            },
          ],
        },
        {
          blocksBehind: 3,
          expireSeconds: 30,
        }
      )
      .then((_) => {
        toast.success(
          "Mine upgrading started, you can mine it when it will be finished"
        );
        setButtonLoader(null);
      })
      .catch((error) => {
        toast.error("Mine doesn't upgraded, try again after one minute");
        setButtonLoader(null);
      });
  };

  const upgradeWithWaxCloud = (nft, levelUp) => {
    if (buttonLoader) return;

    setButtonLoader(nft.asset_id);
    User.wax.api
      .transact(
        {
          actions: [
            {
              account: "atomicassets",
              name: "transfer",
              authorization: [
                {
                  actor: User.wax?.userAccount,
                  permission: "active",
                },
              ],
              data: {
                from: User.wax?.userAccount,
                to: "xcryptochaos",
                asset_ids: [levelUp.asset_id],
                memo: "burn@level@" + nft.asset_id,
              },
            },
          ],
        },
        {
          blocksBehind: 3,
          expireSeconds: 30,
        }
      )
      .then((_) => {
        toast.success(
          "Mine upgrading started, you can mine it when it will be finished"
        );
        setButtonLoader(null);
      })
      .catch((error) => {
        toast.error("Mine doesn't upgraded, try again after one minute");
        setButtonLoader(null);
      });
  };

  const upgradeNft = (nft) => {
    const levelUp = myNfts.filter(
      (nft) => nft.name === "ChaosX-18 Level Up token"
    );
    if (levelUp[0]) {
      if (anchorConnected) {
        upgradeWithAnchor(nft, levelUp[0]);
      } else if (waxConnected) {
        upgradeWithWaxCloud(nft, levelUp[0]);
      }
    } else {
      toast.error("To upgrade your mine purchase ChaosX-18 Level Up token");
      return;
    }
  };
  
  
  
  
  const [timeRemaining, setTimeRemaining] = useState(0);

  // Funzione per avviare il timer e pianificare la ricarica
  const startUpgradeTimer = () => {
    const upgradeTime = new Date(mine.lastUpgrade).toLocaleString();
	console.log("Valore di upgradeTime:", upgradeTime);

    if (upgradeTime === "1.1.1970, 00:00:00") {
	  console.log("Upgrade non in corso");	
      return; // L'aggiornamento non è in corso
    }  

    const neededTime = Math.floor(11 * Math.pow(1.55, mine.level)) * 1000;
	console.log("Valore di neededTime:", neededTime);
    if (neededTime > 0) {
      // Calcola il tempo rimanente e aggiorna lo stato
      setTimeRemaining(neededTime);
	  console.log("Valore di timeRemaining:", timeRemaining);
      // Crea un timer per l'aggiornamento
      const timer = setInterval(() => {
        setTimeRemaining((prevTimeRemaining) => {
          const updatedTimeRemaining = prevTimeRemaining - 1000; // Sottrai un secondo
		  console.log("Valore di updatedTimeRemaining:", updatedTimeRemaining);

          if (updatedTimeRemaining <= 0) {
			console.log("Timer scaduto, ricarica la pagina");  
            // Il timer è scaduto, ricarica la pagina
            clearInterval(timer);
            setTimeout(() => {
              window.location.reload();
            }, 1000);
            return 0; // Imposta il tempo rimanente a zero
          } else {
            // Restituisci il tempo rimanente aggiornato
            return updatedTimeRemaining;
          }
        });
      }, 1000); // Ogni secondo
    }
  };

  // Chiama la funzione per avviare il timer
  startUpgradeTimer();
   
  
  useEffect(() => {
    if (mine) {
      setTimeout(() => {
        dispatch(getMyWorkingNfts(mine.asset_id));
      }, 1000);
    }
    dispatch(getMyNfts());
  }, [dispatch, mine]);
  
  useEffect(() => {
    startUpgradeTimer(); // Avvia il timer quando il componente viene montato
  }, []);

  return (
    <motion.div
      viewport={{ root: scrollRef, once: true }}
      transition={miningTransition}
      variants={Variants}
      initial="initial"
      whileInView="whileInView"
      className={styles.container}
    >
      {myWorkingNfts && (
        <React.Suspense fallback={<Loader size={250} />} key={mine.asset_id}>
          <MyNftCard
            key={mine?.asset_id}
            nft={myWorkingNfts[mine?.asset_id]}
            image={images[myWorkingNfts[mine?.asset_id]?.name]}
            functional={false}
			mineLevel={mine.level}
          />
        </React.Suspense>
      )}
      {mine && (
        <div className={styles.container_miningInfo}>
          <div className={styles.container_miningInfo_aurumBoosters}>
            <p>
              <b>First booster: </b>
              {new Date(mine.bstrOne).toLocaleString() ===
              "1.1.1970, 00:00:00"
                ? "Not yet available"
                : new Date(mine.bstrOne).toLocaleString()}
            </p>
            <p>
              <b>Second booster: </b>
              {new Date(mine.bstrTwo).toLocaleString() ===
              "1.1.1970, 00:00:00"
                ? "Not yet available"
                : new Date(mine.bstrTwo).toLocaleString()}
            </p>
          </div>
          <div className={styles.container_miningInfo_timeInfo}>
            <p>
              <b>Last time claimed: </b>
              {new Date(mine.lastMined).toLocaleString() ===
              "1.1.1970, 00:00:00"
                ? "None"
                : new Date(mine.lastMined).toLocaleString()}
            </p>
            <p>
              <b>Last time upgrade: </b>
              {new Date(mine.lastUpgrade).toLocaleString() ===
              "1.1.1970, 00:00:00"
                ? "Upgrade not in progress"
                : new Date(mine.lastUpgrade).toLocaleString()}
            </p>
			{timeRemaining > 0 && (
			  <p>
			    <b>Time remaining for upgrade: </b>
			    {formatTimeRemaining(timeRemaining)}
			  </p>
			)}			
            <p>
              <b>Unstake time: </b>
              {new Date(mine.unstake_time).toLocaleString() ===
              "1.1.1970, 00:00:00"
                ? "Unstake not in progress"
                : new Date(mine.unstake_time).toLocaleString()}
            </p>
          </div>
          <div className={styles.container_miningInfo_mainInfo}>
            <p>
              <b>Miner level: </b>
              {mine.level}
            </p>
            {/*<p>
              //<b>Miner locked: </b>
              //{mine.is_locked === 0 ? "No" : "Yes"}
            //</p>
            //<p>
              //<b>Miner staked: </b>
              //{mine.is_staked === 0 ? "No" : "Yes"}
            //</p>*/}
          </div>
        </div>
      )}
	  {/*console.log("Valore di timeRemaining dopo il render:", timeRemaining);*/}

      <div className={styles.container_buttonWrapper}>
        <Button onClick={mineNft} loader={buttonLoader} size="fit" color="blue">
          Claim now
        </Button>
        <Button
          onClick={() => upgradeNft(myWorkingNfts[mine?.asset_id])}
          loader={buttonLoader}
          size="fit"
          color="blue"
        >
          Upgrade now
        </Button>
      </div>
    </motion.div>
  );
};

function formatTimeRemaining(timeRemaining) {
  const days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24));
  const hours = Math.floor((timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  const minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000);

  let formattedTime = '';

  if (days > 0) {
    formattedTime += `${days}d `;
  }
  if (hours > 0) {
    formattedTime += `${hours}h `;
  }
  if (minutes > 0) {
    formattedTime += `${minutes}m `;
  }
  formattedTime += `${seconds}s`;

  return formattedTime;
}


export default Mining;
